import React from "react";
import moment from "moment";
import "@vkontakte/vkui/dist/vkui.css";
import "@vkontakte/vkui/";
import Icon56UsersOutline from "@vkontakte/icons/dist/56/users_outline";
import {
  Separator,
  Spinner,
  Button,
  Placeholder,
  Header,
} from "@vkontakte/vkui";
import "./main_style.css";
var rasp = [];
var url;

class Rasp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoaded: false,
      error: "",
     
    };
  }
  async componentDidMount() {
    
    moment.updateLocale("ru", {
      weekdays: [
        "Воскресенье",
        "Понедельник",
        "Вторник",
        "Среда",
        "Четверг",
        "Пятница",
        "Суббота",
      ],
    });
    this.setState({
      isLoaded: false,
    });
    
    var dateBegin = moment().format("D.M.y");
    var days = 7;
    
    //url = "http://services.niu.ranepa.ru/API/public/group/getSchedule";
    if (this.props.id === ""|this.props.type === '3'|typeof this.props.type === 'undefined') {
      rasp = "nan";
      this.setState({
        isLoaded: true,
      });
      return rasp;
    } else{
      
      //url = "http://services.niu.ranepa.ru/API/public/teacher/getSchedule";
      
      try {
        
      //if (this.props.type === '1'|this.props.type === 1) {
        url = "https://ranepaschedule.000webhostapp.com/GetRasp.php";
      //}else{
        //url = "http://services.niu.ranepa.ru/API/public/group/getSchedule";
      //}
      //console.log(this.props.type)
        await fetch(url, {
          method: "POST",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          body:
            "id=" +
            this.props.id +
            "&days=" +
            days +
            "&type="+this.props.type,
        })
          .then((res) => res.json())
          .then((result) => {
            //console.log(result,this.props.type);
            var i = 0;
            rasp = [];
            switch (result.length !== 0) {
              case true:
                

              
                var raspPart = Object.keys(result).map((rasp) => result[i++]);
                i = 0;
                var raspDates = Object.keys(result).map(
                  (raspcell) => result[i++].xdt
                );
                var day = 0;
                var first = true;
                var days = [];
                var oneday = [];
                var lesson_conunter = 0;
                for (var b = 0; raspDates.length > b; b++) {
                  if (first === true) {
                    first = false;
                  }
                  oneday[lesson_conunter] = raspPart[b];
                  lesson_conunter++;

                  days[day] = oneday;
                  rasp[day] = {
                    date: moment(raspPart[b].xdt).format("D.M.y [-] dddd"),
                    day: days[day],
                  };

                  if (raspDates[b] !== raspDates[b + 1]) {
                    oneday = [];
                    lesson_conunter = 0;
                    day++;
                  }
                }
                this.setState({
                  isLoaded: true,
                });
                return rasp;



              default:
                
                rasp = [];
                this.setState({
                  isLoaded: true,
                });
                
                return rasp;
            }
          });
      } catch (error) {
        this.setState({ error: error });
      }
    }
    
  }
  render() {
    
    if ((this.state.isLoaded === true) & (rasp.length !== 0)) {
      
      if (this.props.type === '0'|this.props.type === 0) {
        return (
          <div key={Math.random(1)}>
            {rasp.map((block) => (
              <div key={Math.random(10)}>
                <Header mode="secondary">{block.date}</Header>
                <Separator wide={true} />
                <div key={Math.random(10)}>
                  {block.day.map((celldata) => (
                    <div key={Math.random()}>
                      <table className="RaspTable" key={Math.random(10)}>
                        <tbody>
                          <tr>
                            <td className="RaspTime" key={Math.random()}>
                              {celldata.nf}
                              <br />
                              {celldata.kf}
                            </td>
                            <td className="RaspLesson" key={Math.random()}>
                              {celldata.subject}
                              <br></br>
                              {celldata.type.split(", ")[1] !==
                                this.props.grname &&
                                " (" + celldata.type.split(", ")[1] + ")"}
                            </td>
                            <td className="RaspAudit" key={Math.random()}>
                              {" " + celldata.type.split(", ")[0] + "."}
                              <br></br>
                              {celldata.number}
                            </td>
                            <td className="RaspPrep" key={Math.random()}>
                              {celldata.teacher.split(" ")[0]}
                              <br></br>
                              {celldata.teacher.split(" ")[1]}
                              {celldata.teacher.split(" ")[2]}
                            </td>
                          </tr>
                        </tbody>
                      </table>
                      <Separator wide={true}></Separator>
                    </div>
                  ))}
                </div>
              </div>
            ))}
          </div>
        );
      } else if(this.props.type === '1'|this.props.type === 1){
        return (
          <div key={Math.random(1)}>
            {rasp.map((block) => (
              <div key={Math.random(10)}>
                <Header mode="secondary">{block.date}</Header>
                <Separator wide={true} />
                <div key={Math.random(10)}>
                  {block.day.map((celldata) => (
                    <div key={Math.random()}>
                      <table className="RaspTable" key={Math.random(10)}>
                        <tbody>
                          <tr>
                            <td className="RaspTimeP" key={Math.random()}>
                              {celldata.nf}
                              <br />
                              {celldata.kf}
                            </td>
                            <td className="RaspLessonP" key={Math.random()}>
                              {celldata.subject}
                            </td>
                            <td className="RaspAuditP" key={Math.random()}>
                              {celldata.number}, {celldata.type}.{" "}
                              {celldata.group}
                            </td>
                          </tr>
                        </tbody>
                      </table>
                      <Separator wide={true}></Separator>
                    </div>
                  ))}
                </div>
              </div>
            ))}
          </div>
        );
      }else{
        return (
          <Placeholder
            icon={<Icon56UsersOutline />}
            header="Группа не выбрана"
            action={
              <Button
                size="l"
                onClick={() => {
                  this.props.SearchPop();
                }}
                style={{ backgroundColor: "var(--main)" }}
                key={Math.random()}>
                Выбрать
              </Button>
            }>
            Расписание не доступно
          </Placeholder>
        );
      }
    } else if (this.state.error !== "") {
      return (
        <Placeholder icon={<Icon56UsersOutline />} header="Ошибка">
          Не удалось загрузить расписание
        </Placeholder>
      );
    } else if ((rasp.length === 0) & (this.state.isLoaded === true)) {
      return (
        
        <Placeholder icon={<Icon56UsersOutline />} header="Нет данных">
          Расписание не доступно
        </Placeholder>
      );
    } /*else if (this.props.id === ""|this.props.id === "3"|this.rasp === "nan") {
      return (
        <Placeholder
          icon={<Icon56UsersOutline />}
          header="Группа не выбрана"
          action={
            <Button
              size="l"
              onClick={() => {
                this.props.SearchPop();
              }}
              style={{ backgroundColor: "var(--main)" }}
              key={Math.random()}>
              Выбрать
            </Button>
          }>
          Расписание не доступно
        </Placeholder>
      );
    }*/ else {
     
      return <Spinner size="medium" style={{ marginTop: 20 }} />;
    }
  }
}

export default Rasp;
